.cart-wrap{
  width: 100%;
  max-width: 1315px;
  margin: 0 auto;
  .cart-list-wrap{
    width: 100%;
    background: $white;
    border: 1px solid $light-gray;
    .cart-head-wrap{
      width: 100%;
      padding: 15px 25px;
      border-bottom: 1px solid $light-gray;
      margin-bottom: 20px;
      padding-bottom: 20px;
      min-height: 100px;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      display: flex;
      align-items: center;
      h2{
        font-size: 20px;
        font-weight: 500;
      }
      .right{
        text-align: right;
      }
      .count-purchases{
        padding: 10px 20px;
        background: #fef8e2;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        width: auto;
        display: inline-block;
        box-shadow: 0 7px 8px rgba(186,204,213, .3);
        p{
          font-size: 16px;
          span{
            font-weight: 500;
            font-size: 18px;
          }
        }
      }
    }
    .cart-detail-list{
      width: 100%;

      .single-item-cart{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        width: 100%;
        height: 165px;
        border-bottom: 1px solid $light-gray;
        padding: 15px 25px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        margin-bottom: 15px;
        .logo-wrap{
          width: 20%;
          text-align: center;
          line-height: 165px;
          img{
            max-width: 100%;
            max-height: 100%;
          }
        }
        .title-wrap{
          width: 60%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;

          h2{
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding-right: 15px;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            line-height: 30px;
          }
          p{
            margin-bottom: 10px;
            font-size: 15px;
            font-weight: 500;
          }
          h2>a{
            font-size: 20px;
            color: $blue-btn;
            &:hover{
              text-decoration: underline;
            }
          }
        }
        .price-wrap{
          width: 20%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: flex-start;
          position: relative;
          .price-title{
            font-size: 24px;
            font-weight: 500;
          }
          .count-items-wrap{
            width: 80%;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            input{
              height: 40px;
              width: 45px;
              border: 1px solid $light-gray;
              border-radius: 0;
              text-align: center;
              padding-left: 0;
            }
            span{
              height: 38px;
              padding: 0 15px;
              border: 1px solid $light-gray;
              line-height: 38px;
              text-align: center;
              font-size: 16px;
              font-weight: 500;
              cursor: pointer;
              background: #f5f7f8;
              &:first-child{
                border-top-left-radius: 4px;
                border-bottom-left-radius: 4px;
                border-right: 0;
              }
            }
            .to{
              border-top-right-radius: 4px;
              border-bottom-right-radius: 4px;
              border-left: 0;
            }
          }
          .close{
            position: absolute;
            top: -15px;
            right: 10px;
            display: inline-block;
            width: 20px;
            height: 20px;
            cursor: pointer;
            &:before, &:after {
              position: absolute;
              left: 9px;
              content: ' ';
              height: 20px;
              width: 2px;
              background-color: $light-gray;
            }
            &:before {
              transform: rotate(45deg);
            }
            &:after {
              transform: rotate(-45deg);
            }
          }
        }
        .checkout-wrap{
          display: flex;
          justify-content: flex-end;
          align-items: center;
          width: 100%;
          .btn{
            padding: 0 25px;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            color: $white;
            font-size: 20px;
            font-weight: 500;
            background: $blue-btn;
            border: 1px solid $blue-btn-bd;
            -webkit-border-radius: 4px;
            -moz-border-radius: 4px;
            border-radius: 4px;
            line-height: 40px;
          }
        }
      }
    }
    .cart-footer-action{
      width: 100%;
      height: 80px;
      display: flex;
      align-items: center;
      padding: 0 25px;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      margin-bottom: 15px;
      .right{
        text-align: right;

      }
    }
  }
}

.cart-blocked-wrap{
  position: fixed;
  width: 0;
  height: 100%;
  top: 0;
  right: 0;
  background: #ffffff;
  transition: width .5s ease-out;
  overflow: hidden;
  }
.cart-blocked-wrap.show{
  width: 550px;
  transition: width .5s ease-out;
  z-index: 500;

  .cart-wrap{
    height: 100%;
    &>.close{
      position: absolute;
      top: 15px;
      left: 10px;
      display: inline-block;
      width: 20px;
      height: 20px;
      cursor: pointer;
      &:before, &:after {
        position: absolute;
        left: 9px;
        content: ' ';
        height: 20px;
        width: 2px;
        background-color: $light-gray;
      }
      &:before {
        transform: rotate(45deg);
      }
      &:after {
        transform: rotate(-45deg);
      }
    }
    .cart-list-wrap{
      height: 100%;
      .cart-head-wrap{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 15%;
        .left,.right{
          width: 85%;
        }
      }
      .cart-detail-list{
        overflow: auto;
        height: 70%;
        .single-item-cart{
          padding: 10px;
          height: auto;
          .logo-wrap{
            padding: 10px;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            line-height: 70px;
          }
          .title-wrap{
            width: 55%;
            h2{
              a{
                font-size: 19px;
              }
            }
          }
          .price-wrap{
            width: 25%;
            justify-content: space-around;
            .close{
              top: -11px;
            }
          }
        }
      }
      .cart-footer-action{
        height: 15%;
        .right{
          height: 100%;
          display: flex;
          align-items: center;
          justify-content:flex-end;
        }
      }
    }
  }
}

.cart-checkout-wrap{
  width: 100%;
  max-width: 1315px;
  margin: 0 auto;
  .all-items-cart-wrap{
    width: 100%;
    background: $white;
    &>div{
      width: 100%;
      max-width: 800px;
      margin: 0 auto;
      h2{
        font-size: 24px;
        font-weight: 500;
        padding-top: 35px;
        margin-bottom: 25px;
      }
    }
    .checkout-user-wrap{
      .checked-product{
        width: 100%;
        background: #fef8e2;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 15px 25px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        min-height: 200px;
        max-height: 280px;
        height: 100%;
        -webkit-border-radius: 7px;
        -moz-border-radius: 7px;
        border-radius: 7px;
        margin-bottom: 30px;
        .image-prod{
          width: 150px;
          height: 155px;
          img{
            max-width: 100%;
          }
        }
        .detail-description{
          width: calc(100% - 170px);
          margin-left: 20px;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          height: 155px;

          .head{
            margin-bottom: 30px;
            .title{
              color: $blue-btn;
              font-size: 15px;
              line-height: 26px;
            }
            .shop-title{
              font-weight: 500;
            }
          }
          .footer{
            .price{
              font-size: 24px;
              font-weight: 500;
            }
          }
        }
      }
    }
    .detail-user-fields{
      width: 100%;
      margin-top: 30px;
      border-top: 1px solid $light-gray;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      .field-list{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
        p{
          width: 48%;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          span{
            display: inline-block;
            margin-bottom: 5px;
            font-size: 13px;
          }
        }
        input{
          width: 100%;
          border: 1px solid $light-gray;
        }
      }
    }
    .payment-delivery-wrap{
      width: 100%;
      margin-top: 30px;
      border-top: 1px solid $light-gray;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      textarea{
        width: 100%;
        height: 120px;
        border: 1px solid $light-gray;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
      }
      .other-info{
        p{
          margin-bottom: 5px;
          font-size: 13px;
        }
      }
    }
    .end-section{
      width: 100%;
      margin-top: 30px;
      padding-top: 30px;
      border-top: 1px solid $light-gray;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      padding-bottom: 40px;
      .two-wrap{
        background: #fef8e2;
        width: 100%;
        height: 80px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
        padding: 15px 25px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        .left{
          display: flex;
          justify-content: flex-start;
          flex-direction: column;
          .price{
            font-size: 24px;
            font-weight: 500;
            margin-top: 10px;
          }
        }
        .right{
          display: flex;
          justify-content: flex-end;
          align-items: center;
          button{
            border: 1px solid $blue-btn-bd;
            background: $blue-btn;
            font-weight: 500;
            color: $white;
            padding: 0 15px;
            font-size: 20px;
            -webkit-border-radius: 4px;
            -moz-border-radius: 4px;
            border-radius: 4px;
          }
        }
      }
    }
  }
}